body{
  margin: 100px;
  background-color: #eee;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

/* Whole card */
.card{
  width: 300px;
  height: 320px;
  margin: 0 auto;
  position: relative;
  perspective: 1400px;
  left: 0;
  transition: all 1s;
}

/* Left card */
.left-card{
  position: relative;
  width: 100%;
  height: 100%;
  cursor: pointer;
  box-shadow: 5px 0px 5px rgba(0, 0, 0, 0.1);
  z-index: 1;

  transition: transform 1s;
  transform-style: preserve-3d;
  transform-origin: 0 0;
}
.left-card img{
  width: 100%;
}

/* back and front card */
.left-card .card-front, 
.left-card .card-back{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0; 
  backface-visibility: hidden;
}

/* back of the card */
.left-card .card-back{
  background-color: #f4f4f4;
  transform: rotateY(180deg);
  width: 80%;
  height: 80%;
}
.card-back{
  padding: 30px;
}
.card-back img{
  width: 100%;
  height: 255px;
}
.card-back button{
  color: blue;
  border: 1px solid blue;
}
.card-back button:hover{
  background-color: blue;
  color: #fff;
}
.card-back .btn-data{
  display: flex;
  justify-content: space-around;
}

/* Open card */
.card.open .left-card{
  transform: rotateY(-180deg);
}
.card.open{
  left: 150px;
}
/* Right card */
.right-card{
  width: 100%;
  height: 100%;
  background-color: #fff;
  text-align: left;
  padding: 30px;
  box-sizing: border-box;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
  color: #444;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}
.right-card h2{
  color: crimson;
}